@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"),
    url("../fonts/montserrat-700.woff2") format("woff2"),
    url("../fonts/montserrat-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

