html {
  font-family: sans-serif;
  text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

progress {
  vertical-align: baseline; }

template,
[hidden] {
  display: none; }

a {
  background-color: transparent;
  text-decoration-skip: objects; }

a:active,
a:hover {
  outline-width: 0; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline; }

b,
strong {
  font-weight: bolder; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background-color: #ffff00;
  color: #000000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

button,
input,
select,
textarea {
  font: inherit;
  margin: 0; }

optgroup {
  font-weight: bold; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  appearance: none; }

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54; }

::-webkit-file-upload-button {
  appearance: button;
  font: inherit; }

@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: local("Raleway"), url("../fonts/raleway-800.woff2") format("woff2"), url("../fonts/raleway-800.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira sans";
  src: local("Fira sans"), url("../fonts/fira-sans-400.woff2") format("woff2"), url("../fonts/fira-sans-400.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira sans";
  src: local("Fira sans"), url("../fonts/fira-sans-500.woff2") format("woff2"), url("../fonts/fira-sans-500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira sans";
  src: local("Fira sans"), url("../fonts/fira-sans-700.woff2") format("woff2"), url("../fonts/fira-sans-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  pointer-events: none;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  text-indent: -9999px;
  white-space: nowrap;
  overflow: hidden; }

.header {
  position: relative;
  padding-top: 47px;
  padding-bottom: 146px;
  background-image: url("../img/girl.png"), url("../img/israel.png"), url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%, 50% 28%, 50% 0;
  text-align: center; }

.header__menu {
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  max-width: 1171px; }

.header__logo {
  position: absolute;
  top: 32px;
  left: 50%;
  transform: translateX(-50%); }

.header__contacts {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__menu a[href] {
  font-family: "Fira sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  color: #484848;
  text-transform: uppercase;
  cursor: pointer; }
  .header__menu a[href]:hover {
    color: #ffc341; }
  .header__menu a[href]:active {
    opacity: 0.5; }

.header__call {
  border-bottom: 1px dashed #484848; }
  .header__call:hover {
    border-bottom: 1px dashed #ffc341; }

.header__title {
  margin: 0 auto;
  margin-bottom: 32px;
  padding-top: 472px;
  font-family: "Fira sans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  color: #ffffff;
  max-width: 380px; }

@media (max-width: 1023px) {
  .header {
    background-position: 48% 100%, 50% 28%, 50% 0%;
    background-size: 1255px, 100%, cover; } }

@media (max-width: 767px) {
  .header {
    padding-top: 20px;
    padding-bottom: 16px;
    background-position: 50% -100%, 50% 38%, 50% 0%;
    background-size: 920px, 107%, cover; }
  .header__contacts {
    flex-direction: column;
    align-items: flex-end; }
  .header__logo {
    width: 120px;
    height: 42px;
    top: 20px;
    left: 16px;
    transform: translateX(0); }
  .header__menu a {
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 17px; }
    .header__menu a:last-child {
      margin-bottom: 0; }
  .header__title {
    margin: 0 auto;
    margin-bottom: 32px;
    padding-top: 272px;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 20px;
    line-height: 24px;
    max-width: 380px; } }
